// ** React Imports
import { Link } from 'react-router-dom'

// ** Custom Components
import Avatar from '@components/avatar'

// ** Store & Actions
import { store } from '@store/store'
import { getClient } from '../store'
// ** Reactstrap Imports
import { Badge } from 'reactstrap'

// ** Renders Client Columns
const renderClient = name => {
  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={name ? name?.charAt(0).toUpperCase() : ''}
    />
  )
}

const statusObj = [
  '',
  'light-primary',
  'light-warning'
]

const statusArr = [
  '',
  "Active",
  "Inactive"

]

const stringTrimfn = (text) => {
  return text.length >= 20 ? `${text.substr(0, 20)}...` : text
}

export const columns = [
  {
    name: 'Business Name',
    minWidth: '240px',
    sortable: true,
    sortField: 'name',
    selector: row => row.name,
    cell: row => (row.name !== '' ? <div className='d-flex justify-content-left align-items-center'>
      {renderClient(row.name)}
      <div className='d-flex flex-column'>
        <Link
          to={`/master/${row.id}`}
          className='user_name text-truncate text-body'
          onClick={() => { localStorage.setItem('clientId', row.syncid); store.dispatch(getClient(row.id)) }}
        >
          <span className='fw-bolder'>{row.name === '' ? stringTrimfn(row.contactpersonname || '') : stringTrimfn(row.name || '')}</span>
        </Link>
      </div>
    </div> : <span>-</span>
    )
  },
  {
    name: 'Contact Person',
    sortable: true,
    minWidth: '240px',
    sortField: 'email',
    selector: row => row.contactpersonname,
    cell: row => (<Link to={`/master//${row.id}`}>
      <div className='d-flex justify-content-left align-items-center text-body'>
        {renderClient(row.contactpersonname)}
        <div className='d-flex flex-column'>
          <span className='fw-bolder'>{stringTrimfn(row.contactpersonname || '')}</span>
          <small className='text-truncate text-muted mb-0'>{stringTrimfn(row.email || '')}</small>
        </div>
      </div>
    </Link>
    )
  },
  {
    name: 'Business Entity',
    minWidth: '158px',
    maxWidth: '248px',
    sortable: true,
    sortField: 'entityname',
    selector: row => row.entityname,
    cell: row => <Link to={`/master//${row.id}`}> <span className='text-capitalize text-body'>{stringTrimfn(row.entityname || '')}</span></Link>
  },
  {
    name: 'Contact',
    minWidth: '138px',
    maxWidth: '150px',
    sortable: true,
    sortField: 'contactnumber',
    selector: row => row.contactnumber,
    cell: row => <Link to={`/master//${row.id}`}> <span className='text-capitalize text-body'>{row.contactnumber}</span></Link>
  },
  {
    name: 'Status',
    minWidth: '138px',
    maxWidth: '150px',
    sortable: true,
    sortField: 'status',
    selector: row => statusArr[row.status],
    cell: row => (
      <Link to={`/master//${row.id}`}>
        <Badge className='text-capitalize' color={statusObj[row.status]} pill>
          {statusArr[row.status]}
        </Badge>
      </Link>
    )
  }
]
